@import "../../../styles/utils";

.detected-fm-modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 100vh;
  left: 0;
  width: 100vw;
  height: 100vh;
  visibility: hidden;

  transition: top 1s;
  transition-delay: 0s, 1s;
  transition-property: top, visibility;

  &_is-active {
    top: 30vh;
    visibility: visible;
    transition-delay: 0s, 0s;
    transition-property: top, visibility;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 185px;
    padding: $ai-main-v-padding $ai-main-h-padding;
    background-color: $light-white-color;
    border-radius: $ui-border-radius;
  }

  &__title {
    font-size: 20px;
    font-weight: 500;
    color: $ui-light-green-color;
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow: hidden auto;
  }

  &__paragraph {
    font-size: 16px;
    font-weight: 400;

    &_highlighted {
      color: $ui-light-green-color;
      font-weight: 500;
    }
  }
}

