@import "../../../styles/utils";

.filters-modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 100vh;
  left: 0;
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.25);
  visibility: hidden;

  transition: top 1s;
  transition-delay: 0s, 1s;
  transition-property: top, visibility;

  &_is-active {
    top: 0;
    visibility: visible;
    transition-delay: 0s, 0s;
    transition-property: top, visibility;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    height: 90%;
    max-width: 250px + 20px + $ai-main-h-padding * 2 + $ai-main-h-padding + 6px;
    max-height: 520px;
    padding: $ai-main-v-padding $ai-main-h-padding;
    background-color: $light-white-color;
    border-radius: $ui-border-radius;
  }

  &__title {
    font-size: 20px;
    font-weight: 500;
    color: $ui-light-green-color;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 100%;
    height: 100%;
    padding: 5px;
    overflow: hidden auto;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(0, 0, 0, 0.2);
      border-radius: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background: $ui-light-green-color;
      border-radius: 3px;
    }
  }

  &__block {
    @include gap-margins(10px, 0);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    &_favourite {
      margin-top: auto;
      justify-self: end;
    }
  }

  &__block-title {
    align-self: start;
    padding: 10px 0;
    font-size: 18px;
    font-weight: 500;
    color: $ui-light-green-color;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  &__item {
    @include square(80px);

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 10px 10px 0;
    font-size: 16px;
    font-weight: 500;
    border: 4px solid transparent;
    border-radius: $ui-border-radius;
    box-shadow: 0 0 4px 2px rgba(221, 221, 221, 1);

    &_selected {
      border-bottom: 4px solid $ui-light-green-color;
    }
  }

  &__button {
    @include noBorder;

    width: 200px;
    height: 80px;
    background-color: $light-white-color;
    background-image: url("../../../images/ui/control-panel/star-unfilled.png");
    background-repeat: no-repeat;
    background-size: 32px;
    background-position: 5px center;
    box-shadow: 0 0 4px 2px rgba(221, 221, 221, 1);
    border: 4px solid transparent;
    border-radius: $ui-border-radius;

    &_active {
      background-image: url("../../../images/ui/control-panel/star-filled.png");
      border-bottom: 4px solid $ui-light-green-color;
    }
  }

  &__button-name {
    font-size: 18px;
    font-weight: 500;
  }

  &__button-description {
    font-size: 14px;
    font-weight: 400;
    color: $light-gray-color;
  }

  &__image {}
}
