@import "../../styles/utils";

.glasses-list {
  display: flex;
  flex-direction: column;
  z-index: 0;
  position: relative;

  @media (min-width: 720px) {
    max-height: calc(100vh - 300px);
    padding: 20px;
    overflow: hidden auto;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(0, 0, 0, 0.2);
      border-radius: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background: #9c9c9c;
      border-radius: 3px;
    }
  }

  &__item {
    @include box-element;

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $ai-main-v-padding;
    padding: $ai-main-v-padding $ai-main-h-padding;
    height: 63px;
    cursor: pointer;
    transition: 0.2s;

    &:hover{
      transform: scale(1.05);
    }

    &_selected {
      font-weight: 500;
      transform: scale(1.05);
    }
  }

  &__description-container {
    display: flex;
    align-items: center;
  }

  &__preview-container {
    @include square($ai-square-size);
    @include box-element;

    margin-right: 13px;
  }

  &__preview-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: center;

    text-align: center;
    font-size: 12px;
  }

  &__title {
    font-family: inherit;
    font-weight: inherit;
  }

  &__is-local {
    @include square($ai-square-size);
    @include normalize-background;
    @extend %noBorder;

    background-image: url("../../images/admin/add-new-glasses/local.png");
    background-size: 16px 16px;
  }

  &__option-container {
    display: flex;
    justify-content: space-between;
  }

  &__option-button {
    @include normalize-background;
    @include square(18px);
    @include noBorder;
    @include gap-margins(0, 24px);
    transition: transform 0.2s;
    cursor: pointer;

    &:hover{
      transform: scale(1.2) !important;
    }

    &:active {
      transform: scale(0.9);
    }

    &_edit {
      background-image: url("../../images/admin/add-new-glasses/edit.png");
    }

    &_delete {
      background-image: url("../../images/admin/add-new-glasses/delete.png");
    }
  }
}
