@import "../../styles/utils";

.control-panel {
  z-index: 1000;

  &__panel-container {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    left: 0;
    bottom: 0;
    width: calc(100% - 20px);
  }

  &__glasses-list {
    display: flex;
    justify-content: start;
    align-items: center;
    margin-bottom: 10px;
    max-width: 100%;
    padding-bottom: 6px;

    overflow: auto hidden;

    @media (min-width: 1080px) {
      max-width: 1080px;
    }

    &::-webkit-scrollbar {
      height: 6px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(0, 0, 0, 0.2);
      border-radius: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background: $ui-light-green-color;
      border-radius: 3px;
    }
  }

  &__glasses-item {
    @include gap-margins(0, 10px);
    @include square(60px);

    display: flex;
    flex-shrink: 0;
    background-color: rgba(255, 255, 255, 0.25);
    border: 4px solid transparent;
    border-radius: $ui-border-radius;

    &_active {
      border-bottom: 4px solid $ui-light-green-color;
    }
  }

  &__glasses-image {
    width: 100%;
    height: auto;
  }

  &__navbar {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 360px;
  }

  &__nav-button {
    @include square($ui-additional-button-size);
    @include noBorder;
    @include normalize-background;

    border-radius: 50%;
    background-color: $light-white-color;
    background-size: 60%;

    transition: scale 0.3s ease-out, box-shadow 0.3s ease-out;

    &:hover {
      transform: scale(1.05);
      box-shadow: 0 0 15px 5px rgba(221, 221, 221, 1);

      transition: scale 0.1s ease-out, box-shadow 0.1s ease-out;
    }

    &:active {
      transform: scale(0.95);
      box-shadow: 0 0 30px 10px rgba(139, 196, 65, 1);
    }

    &_central {
      @include square($ui-main-button-size);
      background-size: 80%;
    }

    &_filter {
      background-image: url("../../images/ui/control-panel/eye-glasses.png");
    }

    &_snapshot {
      background-image: url("../../images/ui/control-panel/snapshot.png");
    }

    &_favourite {
      background-image: url("../../images/ui/control-panel/star-unfilled.png");
    }

    &_is-favourite {
      background-image: url("../../images/ui/control-panel/star-filled.png");
    }
  }
}
