@import "../../styles/utils";

.range-and-number {
  @include gap-margins(10px, 0);

  display: flex;
  width: 100%;
  flex-direction: column;

  &__title {
    font-family: inherit;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
  }

  &__container {
    display: flex;
    width: 100%;
  }

  &__input {
    &_range {
      width: 100%;
      margin-right: 10px;
    }

    &_number {
      border-radius: 3px;
      border: 2px solid rgba(#000, 0.1);
      padding: 3px 12px;

      &:focus {
        outline-color: $ui-light-green-color;
      }
    }
  }
}
