@import "../../styles/utils";

.edit-container {
  position: relative;
  display: flex;

  &_desktop {
    &:before {
      position: absolute;
      content: '';
      width: 20px;
      height: 100%;
      left: 0;
      background-color: $light-white-color;
      transform: translate(-100%, 0%);
      z-index: -1;
    }
  }

  &__left-sidebar {

    flex-grow: 1;
    flex-basis: 700px;
    height: calc(100vh - 59px);
    margin-right: 10px;

    background-color: white;
    border-radius: 0 7pxs 7pxs 0;
  }

  &__main-content {
    flex-grow: 4;
    flex-basis: 100%;
  }
}
