@mixin box-element {
  background-color: $light-white-color;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
  border-radius: 4px;

  &_selected {
    background-color: #fff;
    box-shadow: 0 5px 0px rgba($ui-light-green-color, 0.9), 0 6px 32px rgba(0, 0, 0, 0.2);
    z-index: 100;
  }
}

@mixin selectedScene {
  border: 2px solid transparent;
  
  &_selected {
    filter: blur(0px) !important;
  }
}

@mixin completed($size, $right, $bottom) {
  &::after {
    @include box-element;
    @include square($size);
    @include normalize-background;

    content: "";
    position: absolute;
    right: $right;
    bottom: $bottom;
    background-color: #14d114;
    background-image: url("../../images/admin/edit/completed.png");
    opacity: 0;
    transition: opacity 0.3s;
  }

  &_completed {
    &::after {
      opacity: 0.7;
    }
  }
}

@mixin aborted($size, $right, $bottom) {
  &::before {
    content: "";
    @include box-element;
    @include square($size);
    @include normalize-background;

    position: absolute;
    right: $right;
    bottom: $bottom;
    background-color: #d11414;
    background-image: url("../../images/admin/edit/aborted.png");
    opacity: 0;
    transition: opacity 0.3s;
  }

  &_aborted {
    &::before {
      opacity: 0.9;
    }
  }
}

@mixin noBorder {
  border: none;
  outline: none;
}

@mixin square($dim) {
  width: $dim;
  height: $dim;
}

@mixin input {
  border: none;
  border-bottom: 1px solid #4d457b;
  font-family: inherit;
  font-size: 0.9rem;
  padding: 10px 5px;
  color: #4d457b;
  outline: none;
}

@mixin top-control-box {
  padding: 4px 11px;
  border: none;
  outline: none;
  background: #d9d9d9;
  border-radius: 5px;

  font-size: 14px;
}

@mixin gap-margins($v, $h) {
  margin-right: $h;
  margin-bottom: $v;

  &:last-child {
    margin-right: 0;
    margin-bottom: 0;
  }
}

@mixin normalize-background {
  background-size: contain;
  background-position: center;
  background-color: transparent;
  background-repeat: no-repeat;
}

@mixin gradient-pulse {
  background: linear-gradient(270deg, #ffffff, #dfdfdf, #ffffff);
  background-size: 1000% 1000%;

  animation: AnimationName 1s ease infinite;

  @keyframes AnimationName {
    0%{background-position:100% 51%}
    100%{background-position:0 50%}
  }
}

@mixin preloader-block {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: -100vw;
  visibility: hidden;
  color: $ui-light-green-color;
  tab-index: -1;

  transition: left 1s;
  transition-delay: 0s, 1s;
  transition-property: left, visibility;

  &_active {
    left: 0;
    visibility: visible;
    transition-delay: 0s, 0s;
    transition-property: left, visibility;
  }

  &_wait {
    left: 100vw;
    visibility: hidden;
    transition-delay: 0s, 1s;
    transition-property: left, visibility;
  }

  &__container {
    @include noBorder;

    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: $ui-block-padding;
    background-color: $light-white-color;
    border-radius: $ui-border-radius;

  }

  &__title {
    margin-bottom: 10px;
  }

  &__list {
    @include gap-margins($ui-gap, 0);

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__item {
    @extend %noBorder;
    @include normalize-background;

    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: $ui-block-padding;

    &:not(&:last-child):after{
      content: '';
      position: absolute;
      right: -(($ui-gap / 2) + 1px);
      width: 2px;
      height: 100%;
      background-color: $ui-light-green-color;
    }

    &_active {
      &:before {
        content: '';
        position: absolute;
        bottom: 0;
        width: 20px;
        height: 6px;
        background-color: $ui-light-green-color;
      }
    }
  }

  &__item-image {
    object-fit: cover;
    object-position: center;
    filter: invert(100%);
  }

  &__item-title {
    color: #000;
    font-weight: 500;
  }
}
