@import "../../styles/utils";

.simple-nav {
  $nav-size: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: $nav-size;

  &__button {
    @include square($ui-main-button-size);
    @include normalize-background;
    @include noBorder;

    position: absolute;
    background-color: $light-white-color;
    border-radius: 50%;
    box-shadow: 0 0 4px 2px rgba(221, 221, 221, 1);
    background-size: 40%;

    transition: scale 0.3s ease-out, box-shadow 0.3s ease-out;

    &_left {
      @include square($ui-additional-button-size);

      transform: translate(-(($ui-main-button-size / 2) + ($ui-additional-button-size / 2) + 10px), ($nav-size / 2) + $ui-block-padding);

      &:hover {
        transform: translate(-(($ui-main-button-size / 2)
        + ($ui-additional-button-size / 2) + 10px), ($nav-size / 2)
        + $ui-block-padding) scale(1.05);
        box-shadow: 0 0 15px 5px rgba(221, 221, 221, 1);

        transition: scale 0.1s ease-out, box-shadow 0.1s ease-out;
      }

      &:active {
        transform: translate(-(($ui-main-button-size / 2)
        + ($ui-additional-button-size / 2) + 10px), ($nav-size / 2)
        + $ui-block-padding) scale(0.95);
        box-shadow: 0 0 30px 10px rgba(139, 196, 65, 1);
      }
    }

    &_center {
      @include square($ui-main-button-size);

      transform: translate(0, ($nav-size / 2) + $ui-block-padding);

      &:hover {
        transform: translate(0, ($nav-size / 2) + $ui-block-padding) scale(1.05);
        box-shadow: 0 0 15px 5px rgba(221, 221, 221, 1);

        transition: scale 0.1s ease-out, box-shadow 0.1s ease-out;
      }

      &:active {
        transform: translate(0, ($nav-size / 2) + $ui-block-padding) scale(0.95);
        box-shadow: 0 0 30px 10px rgba(139, 196, 65, 1);
      }
    }

    &_previous {
      background-image: url("../../images/ui/simple-navigation/back.png");
    }

    &_next {
      background-image: url("../../images/ui/simple-navigation/next.png");
    }

    &_close {
      background-image: url("../../images/ui/simple-navigation/close.png");
    }
  }
}
