@import "../../styles/utils";

.edit-glasses {
  &__views-container {
    position: relative;
  }

  &__top-panel {
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: $ai-main-v-padding $ai-main-h-padding;
    z-index: 203;
    border-bottom: 2px solid rgba(black, 0.3);
    background-color: #fff;
    border-radius: 4px 4px 0 0;
  }

  &__input {
    border-radius: 3px;
    border: 2px solid rgba(#000, 0.1);
    padding: 3px 12px;

    &:focus{
      outline-color: $ui-light-green-color;
    }
  }

  &__button {
    @include completed(20px, -10px, -10px);
    @include aborted(20px, -10px, -10px);
    padding: 6px 18px;
    border-radius: 3px;
    border: 2px solid #8bc441;
    background-color: #cbe4ab;
    color: rgb(50, 50, 50);
    font-weight: bold;
    cursor: pointer;
    transition: 0.2s;

    &:hover{
      transform: scale(1.1);
    }

    position: relative;

    &_inactive{
      opacity: 0.5;
    }
  }
  
  &__scene {
    @include box-element;
    @include selectedScene;

    position: relative;
    height: $ai-scene-height;
    margin-bottom: $ai-margin-bottom;
    box-sizing: content-box;
    transition: 0.2s;
  }

  &__preview-scene {
    @include square(10vh);
    @include box-element;
    @include selectedScene;

    transition: 0.2s;

    position: absolute;
    bottom: $ai-main-v-padding;
    left: $ai-main-h-padding;
    box-sizing: content-box;
  }

  &__params-container {
    width: 100%;
  }

  &__params-title {
    margin-bottom: 10px;
    font-family: inherit;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  &__params-list {
    @include box-element;

    display: flex;
    flex-direction: column;
    padding: $ai-main-v-padding $ai-main-h-padding;
    margin-bottom: $ai-block-item-margin;
    max-height: $ai-params-list-height;
    z-index: 1;
    position: relative;
    box-shadow: 0 0 30px rgba(black, 0.2);
  }

  &__params-group {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: auto;
  }

  &__navigation-panel {
    @include box-element;
    display: flex;
    padding: $ai-main-v-padding $ai-main-h-padding;
    width: auto;
    display: inline-block;
    z-index: 0;
    position: relative;
    padding-bottom: 20px;
    margin-bottom: -10px;

    background: #ffffff;
    border-radius: 3px;
  }
}
