@import "../../styles/utils";

.file-upload {
  &__area {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 30vh;

    @include box-element;
    background: $light-white-color;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
    border-radius: 6px;
    transition: 0.2s;

    cursor: pointer;

    &:hover{
      box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25), inset 0 0px 30px rgba(0, 0, 0, 0.25);
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include gap-margins(10px, 0);
  }

  &__title {
    font-size: 20px;
    font-weight: 400;
    color: $light-gray-color;
  }

  &__description {
    font-size: 14px;
    color: $light-gray-color;
    text-align: center;
  }

  &__errors-dropdown {
  }
}
